.album-card_img {
    position: relative;
    background: #171819;
}

.album-card_img img {
    width: 100%;
}

.a-coluna {
    float: left;
    position: relative;
    padding-left: 10px;
    padding-right: 10px;
    width: 100%;
}

.a-card {
    margin-bottom: 30px;
    border-top: 1px solid #18191a;
    padding-top: 20px;
    height: auto;
    min-height: 450px;
}

.a-per-colum {
    width: 20%;
}

@media (max-width: 1050px) {
    .a-per-colum {
        width: 25%;
    }
}

@media (max-width: 850px) {
    .a-per-colum {
        width: 33%;
    }
}

@media (max-width: 650px) {
    .a-per-colum {
        width: 50%;
    }

    .a-card {
        min-height: 0;
    }

    .a-info {
        display: none;
    }
}

@media (max-width: 450px) {
    .a-per-colum {
        width: 100%;
    }

    .a-card {
        min-height: 0;
    }

    .a-info {
        display: none;
    }
}



